<template>
  <div class="area">
       <condition-frame class="op-query">
      <!--表单布局 主要是为了条件区域进行查询-->
        <el-form :model="formData" :inline="true" ref="formData" label-width="0">
          <el-form-item prop="selectedRegions">
                  <el-cascader
                    class="sidebar-search"
                    v-model="formData.selectedRegions"
                    :options="cityList"
                    change-on-select
                    @change="regionChange"
                    :props="props"
                    placeholder="请选择-省/市/区"
                  ></el-cascader> 
              </el-form-item>
              <el-form-item prop="parklotId"> 
                   <el-select
                    class="sidebar-search"
                    v-model="formData.parklotId"
                    filterable
                    remote
                    reserve-keyword
                    placeholder="请输入车场名称"
                    :remote-method="getParklotList"
                    :loading="loading">
                    <el-option
                      v-for="item in formData.parklotList"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id">
                    </el-option>
                  </el-select>
              </el-form-item>
          <el-form-item>
            <!-- <router-link to="/addplace"> -->
                <el-button type="primary" @click="fetchData" size="small">查询</el-button>
                <el-button @click="resetForm('formData')"  size="small">重置</el-button>
                <el-button type="primary" size="small" @click="increVisible =!increVisible">添加</el-button>
                <el-button type="primary" size="small" @click = "exportVisble = !exportVisble" >导出</el-button>
            <!-- </router-link> -->
          </el-form-item>
        </el-form>
    </condition-frame>
    <info-frame>
      当前总条数
      <op-badge>{{totalCount}}</op-badge>
      条
    </info-frame>
    <grid-frame>
      <template>
        <el-table
          :data="datas"
           border
          :height = "theight"
          ref="table"
          stripe
          v-loading.body="listLoading" element-loading-text="拼命加载中"  highlight-current-row fit cell-class-name = "h40"
          >
          <el-table-column
            prop="regionName"
            label="城区"
            width="180"
            class-name="text">
          </el-table-column>
          <el-table-column
            prop="parklotName"
            label="停车场名称"
            width="280">
          </el-table-column>
          <el-table-column
            prop="areaName"
            label="区域名称">
          </el-table-column>
          <el-table-column
            label="泊位个数" 
            prop="spaceCount"
            class-name="num">
          </el-table-column>
          
          <el-table-column fixed="right" label="操作" width="240">
                  <template slot-scope="scope">
                      <el-button type="text" size="small" @click = "areaDetail(scope.row)">详情</el-button>
                      <el-button type="text" size="small" @click = "removeArea(scope.row)">删除</el-button>
                  </template>
              </el-table-column>
        </el-table>
      </template>
      <pagination></pagination>
    </grid-frame>
    <!-- 导出 -->
      <exportExcel :exportHeader="exportHeader" :exportFilter="exportFilter" :dialogVisble.sync="exportVisble" @getExportSize="getExportSize" :getexportList = "getexportList"></exportExcel> 
      <!-- 添加区域开始 -->
      <el-dialog title="区域添加" :visible.sync="increVisible"  width="42%" class="w800" @close = "isAddStaff('addForm')">
          <el-form :inline="true" ref="addForm" :model="addForm" :rules="addrules" label-width="160px"  class="demo-form-inline">
              <el-form-item label="城区：" prop="selectedRegions">
                  <el-cascader
                    v-model="addForm.selectedRegions"
                    :options="cityList"
                    change-on-select
                    :props="props"
                    placeholder="请选择-省/市/区"
                  ></el-cascader> 
              </el-form-item>
              <el-form-item label="停车场：" prop="parklotId"> 
                   <el-select
                    v-model="addForm.parklotId"
                    filterable
                    remote
                    reserve-keyword
                    placeholder="请输入车场名称"
                    :remote-method="getParklotList"
                    :loading="loading">
                    <el-option
                      v-for="item in addForm.parklotList"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id">
                    </el-option>
                  </el-select>
              </el-form-item>
              <el-form-item label="区域：" prop="areaName">
                  <el-input v-model="addForm.areaName" placeholder="请输入" ></el-input>
              </el-form-item>
              <!-- <el-form-item label="真实姓名" prop="trueName">
                  <el-input v-model="ruleForm.trueName" placeholder="请输入"></el-input>
              </el-form-item>
              <el-form-item label="性别" prop="sex">
                  <el-select v-model="ruleForm.sex" placeholder="请选择">
                      <el-option label="请选择" value="0"></el-option>                    
                      <el-option label="男" value="1"></el-option>
                      <el-option label="女" value="2"></el-option>
                  </el-select>
              </el-form-item>
              <el-form-item label="手机号码" prop="mobile">
                  <el-input v-model="ruleForm.mobile" placeholder="请输入"></el-input>
              </el-form-item>
           
              <el-form-item label="邮箱" prop="email">
                  <el-input v-model="ruleForm.email" placeholder="请输入"></el-input>
              </el-form-item> -->
          </el-form>
          <span slot="footer" class="dialog-footer">
              <el-button @click="increVisible=false">取 消</el-button>
              <el-button type="primary" @click="addStaff('ruleForm')">确 定</el-button>
            </span>
      </el-dialog> 
      <!-- 添加区域结束   -->
  </div>
</template>
<script>
import conditionFrame from 'components/base/conditionFrame.vue'
import opBadge from 'components/base/opBadge.vue'
import infoFrame from 'components/base/infoFrame.vue'
import gridFrame from 'components/base/gridFrame.vue'
import exportExcel from 'components/base/exportexcel/index'
import pagination from 'components/base/pagination/index.vue'
import { mapGetters } from 'vuex'
import { getParkAreaList } from '@/api/resource/area'
import { getParklotList } from '@/api/base'
export default {
  name: 'region',
  data () {
    const svalidRegion = (rule, value, callback) => {
      if (!value || value.length === 0) {
        callback(new Error('请选择省市区'))
      } else {
        callback()
      }
    }
    return {
      datas: [],
      totalCount: 0,
      listLoading: true,
      loading: true,
      exportVisble: false,
      increVisible: false,
      formData: {
        regionId: '',
        parklotId: '',
        parklotName: '',
        selectedRegions: [],
        parklotList: []
      },
      addForm: {
        selectedRegions: [],
        parklotId: '',
        parklotList: '',
        areaName: ''
      },
      addrules: {
        userType: [
          {required: true, message: '请选择用户类型', trigger: 'change'}
        ],
        trueName: [
          {required: true, message: '请输入真实姓名', trigger: 'blur'}
        ],
        sex: [
          {required: true, message: '请选择性别', trigger: 'change'}
        ],
        selectedRegions: [
          {required: true, validator: svalidRegion, trigger: 'change'}
        ]
      },
      props: {
        label: 'name',
        value: 'id',
        children: 'list'
      },
      exportHeader: [],
      exportFilter: []
    }
  },
  components: {conditionFrame, infoFrame, gridFrame, exportExcel, pagination, opBadge},
  computed: {
    ...mapGetters(['pageNum', 'pageSize', 'cityList']),
    theight () {
      return document.body.offsetHeight - 300
    }
  },
  watch: {
    pageSize () {
      this.fetchData()
    },
    pageNum () {
      this.fetchData()
    }
  },
  created () {
    this.$store.dispatch('Get_CITYLIST')
    this.fetchData()
  },
  methods: {
    /** ================================= 获取人员列表================================= */
    async fetchData () {
      this.listLoading = true
      this.formData.regionId = this.formData.selectedRegions.slice(-1).join(',')
      this.listQuery = {
        pageNo: this.pageNum,
        pageSize: this.pageSize,
        regionId: this.formData.regionId,
        parklotId: this.formData.parklotId
      }
      let res = await getParkAreaList(this.listQuery)
      if (res.returnObject) {
        this.datas = res.returnObject.list
        this.totalCount = res.returnObject.totalCount
        this.$store.dispatch('SET_TOTALCOUNT', this.totalCount)
      } else {
        this.datas = []
        this.totalCount = 0
        this.$store.dispatch('SET_TOTALCOUNT', 0)
      }
      this.listLoading = false
    },
    /** ================================= 设置角色================================= */
    // 查询车场列表
    async getParklotList (query) {
      this.formData.regionId = this.formData.selectedRegions.slice(-1).join(',')
      if (query !== '') {
        this.loading = true
        this.listQuery = {
          parkingId: '',
          parkingName: query,
          regionId: this.formData.regionId,
          pageNo: 1,
          pageSize: 100
        }
        let res = await getParklotList(this.listQuery)
        if (res.returnObject) {
          this.formData.parklotList = res.returnObject.list
        }
        this.loading = false
      } else {
        this.formData.parklotList = []
      }
    },
    regionChange () {

    },
    areaDetail () {

    },
    removeArea () {

    },
    // 重置查询表单
    resetForm (formName) {
      this.$store.dispatch('SET_PAGESIZE', 15)
      this.$store.dispatch('SET_PAGENUM', 1)
      this.$refs[formName].resetFields()
      this.fetchData()
    },
    // 导出
    getExportSize () {
      const tableData = this.$refs.table.$slots.default
      tableData.forEach((ele) => {
        if (ele.componentOptions && ele.componentOptions.propsData.prop) {
          let propData = ele.componentOptions.propsData
          this.exportHeader.push(propData.label)
          this.exportFilter.push(propData.prop)
        }
      })
    },
    getexportList (size) {
      let listQuery = {
        regionId: this.formData.regionId,
        parklotId: this.formData.parklotId,
        pageNo: this.pageNum,
        pageSize: size
      }
      return new Promise((resolve, reject) => {
        getParkAreaList(listQuery).then((res) => {
          let list = res.returnObject.list
          resolve(list)
        }, err => reject(err))
      })
    }
  }
}
</script>



