import fetch from '@/utils/fetch'

// 获取区域列表
export function getParkAreaList ({pageNo, pageSize, parklotId, regionId}) {
  return fetch({
    url: '/area/getParkAreaList',
    method: 'get',
    params: {
      pageNo,
      pageSize,
      parklotId,
      regionId
    }
  })
}



